h1,
h2,
h3 {
  margin: 0px; }

ul {
  list-style: none;
  margin: 0;
  padding: 0px; }

a {
  outline: none;
  text-decoration: none;
  opacity: 1;
  filter: alpha(opacity=100);
  zoom: 1;
  cursor: pointer; }
  a:-webkit-any-link {
    text-decoration: none !important; }
  a:link {
    text-decoration: none; }
  a:visited {
    text-decoration: none; }
  a:hover {
    text-decoration: none; }
  a:active {
    text-decoration: none; }
  a:hover {
    opacity: 0.5;
    filter: alpha(opacity=50);
    zoom: 1; }

body {
  font-family: "TT Lakes Regular";
  min-width: 320px;
  overflow-x: hidden;
  margin: 0; }

button {
  border: none;
  outline: none;
  background-color: inherit;
  cursor: pointer;
  padding: 0;
  opacity: 1;
  filter: alpha(opacity=100);
  zoom: 1; }
  button:hover {
    opacity: 0.5;
    filter: alpha(opacity=50);
    zoom: 1; }

@media (min-width: 1366px) {
  .base__container {
    display: block;
    width: 79%; } }

@media (min-width: 1440px) {
  .base__container {
    flex: 1 1 1120px; } }

